import React from 'react'
import { ReactElement } from 'react'

import { SubjectSummary } from '../../components/subject/SubjectSummary'
import { SubjectContent } from '../../components/subject/SubjectContent'
import { SubjectTitle } from '../../components/subject/SubjectTitle'
import { SubjectKeyword } from '../../components/subject/SubjectKeyword'
import { SubjectIdentifier } from '../../components/subject/SubjectIdentifier'
import { Subject } from '../../components/subject/Subject'

export function Generalities () : ReactElement {
  return (
    <Subject>
      <SubjectIdentifier>alchemy-generalities</SubjectIdentifier>
      <SubjectKeyword>Alchimie</SubjectKeyword>
      <SubjectTitle>Généralités</SubjectTitle>
      <SubjectSummary>

      </SubjectSummary>
      <SubjectContent>
        <p>
          Certaines notions assez générales sont valable que l'on cherche à
          produire une potion, un poison, une huile, de la poudre, un sel, un
          crystal ou un gaz. Cette partie propose de faire le tour de l'ensemble
          de ces notions générales.
        </p>

        <p>
          Toute substance alchimique est le résultat de plusieurs transformation
          appliquée à de la matière. Cette matière transformée peut alors être
          divisée en deux catégories : si l'on parle de plante, de roche, de
          viande, d'os, de peau ou de tout autre matière d'orgine animale on
          parle alors d'élément primitif, au contraire, si l'on parle du
          résultat d'une transformation quelconque on parle alors d'essence.
        </p>
      </SubjectContent>
    </Subject>
  )
}
